* {
  box-sizing: border-box;
}



body {
  box-sizing: border-box;
  font-family: Shadows Into Light ,sans-serif;
  background-image: url(/seat-selection/static/media/halftone-yellow.7239b597.png);
}


#root {
  display: block;
  padding: 2rem;
  color: black;
}

.full-bloom {
  background-image: url(/seat-selection/static/media/full-bloom.c614347a.png);
  border-radius: 2px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.16), 0 10px 10px rgba(0, 0, 0, 0.23);
  padding: 0.5rem;
}

.main-page-container {
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
}

.main-item-title {
  padding-bottom: 0.5rem;
}

.main-container {
  display: -webkit-flex;
  display: flex;
  margin-top: 2rem;
  margin-bottom: 2rem;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.main-button {
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.16), 0 10px 10px rgba(0, 0, 0, 0.23);
}

.main-item {
  text-align: center;
  padding: 0.5rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.disclaimer-page-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
}

.disclaimer-list > * {
  list-style: none;
}

.disclaimer-button-panel {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column-reverse;
          flex-direction: column-reverse;
  padding-left: 3rem;
  padding-right: 3rem;
  margin-top: 1rem;
  -webkit-align-items: center;
          align-items: center;
}

.confirm-button {
  margin-bottom: 1rem;
}

.open-id-wiki {
  margin-top: 1.5rem;
}

.open-id-help {
  margin-top: 1.5rem;
}

.collection-page-container {
  text-align: center;
}

.collection-page-title-panel {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 1rem;
}

.collection-page-button-panel {
  margin-top: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.collection-page-button-panel button {
  margin: 0.2rem;
}

.seat-selection-page-container {
  text-align: center;
}

.seat-selection-page-title-panel {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 1rem;
}

.option-item li {
  list-style: none;
}

.option-item:first-child {
  margin-bottom: 1rem;
}

.seat-self-selection-status {
  border-radius: 2px;
  margin-top: 1rem;
  padding: 0.5rem;
}

.self-selection-panel {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.warning-list {
  margin-top: 1rem;
}

.auto-selection-page {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
}

.automation-middle-part {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.auto-option-item:nth-child(2) {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.auto-start-button {
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.16), 0 10px 10px rgba(0, 0, 0, 0.23);
}

.auto-end-button {
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.16), 0 10px 10px rgba(0, 0, 0, 0.23);
}



#reservation-log {
  overflow-y: scroll;
  height: 300px;
  padding: 1rem;
}

@media(min-width: 540px) {
  html {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
  }
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

